.title-contact {
    font-size: 60px;
    font-weight: 500;
}

.details {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    .container-form {
        width: 450px;
        height: 380px;
        margin-bottom: 100px;
    }
    .conteiner-map {
        width: 450px;
        height: max-content;
        margin-bottom: 50px;
        iframe{
            width: 100%;
            height: 300px;
            border: none;
            box-shadow: 0 0 10px 5px #2225;
        }
        .info {
            margin-left: 30px;
            text-align: left;
            font-weight: 500;
            p {
                .grafica {
                    color: #222;
                    font-weight: 600;
                    &:hover{
                        color: #4558BD;
                    }
                }
            }
            .link-social {
                width: 100%;
                display: flex;
                flex-direction: row;
                justify-content: space-around;
                .link-redes {
                    color: #222;
                    font-weight: 600;
                    &:hover {
                        color: #4558BD;
                    }
                }
            }
        }
    }
}

@media screen and (max-width:650px) {
    .title-contact {
        font-size: 10vw;
    }
    .details {
        .container-form {
            width: 100vw;
            height: 400px;
            margin-bottom: 50px;
        }
        .conteiner-map {
            width: 100vw;
            height: 350px;
            padding-bottom: 100px;
            iframe{
                width: 100%;
                height: 100%;
                border: none;
                box-shadow: 0 0 10px 5px #2225;
            }
            .info {
                margin-left: 10px;
            }
        }
    }
}