.imagen {
    width: 100%;
    height: 450px;
    background-image: url('../recursos/img-portada.png');
    background-size: auto;
    background-attachment: fixed;
    background-repeat: no-repeat;
    position: relative;
}

.clip {
    width: 100%;
    height: 100px;
    position: absolute;    
    background-color: #d7d7d7;
    clip-path: polygon(100% 26%, 100% 100%, 0 100%, 0 26%, 4% 37%, 8% 46%, 14% 56%, 22% 66%, 30% 73%, 41% 79%, 57% 79%, 68% 73%, 76% 66%, 83% 57%, 89% 48%, 95% 38%);
    bottom: 0;
}

.centro {
    width: 600px;
    height: 350px;
    margin: auto;
    padding-top: 30px;
    padding-bottom: 30px;
    border-radius: 25px;
    background-color: #d7d7d733;
    backdrop-filter: blur(5px);
}

.img {
    width: 450px;
    transition: .3s;
}

.h4 {
    margin: 0;
    font-size: 45px;
    color: #AF543F;
    text-shadow: 0 0 2px #222;
}

.h5 {
    margin: 0;
    font-size: 25px;
    color: #D4842D;
    text-shadow: 0 0 2px #222;
}

.telefonos {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: #FEF155;
}

.p {
    font-size: 18px;
    text-shadow: 0 0 2px #111;
}

.wsp {
    width: 30px;
    height: 30px;
    filter: drop-shadow(0 0 2px #222);
}

@media screen and (max-width:850px) {
    .centro {
        width: 65vw;
        height: max-content;
        padding-bottom: 0px;
    }
    .img {
        width: 40vw;
    }
    .imagen {
        height: 50vw;
    }
    .h4 {
        font-size: 5vw;
    }
    .h5 {
        font-size: 3vw;
    }
    .p{
        font-size: 2.5vw;
    }
}

@media screen and (max-width:650px){
    .centro {
        min-height: 190px;
        min-width: max-content;
    }
    .imagen {
        height: 60vw;
        min-height: 300px;
        transition: .3s;
    }
}

@media screen and (max-width:500px){
    .imagen {
        height: 65vw;
        min-height: 280px;
    }
    .wsp {
        width: 5vw;
        height: 5vw;
    }
    .img {
        min-width: 200px;
    }
    .h4{
        -webkit-text-stroke: 0;
    }
    .h5{
        -webkit-text-stroke: 0;
    }
    .p{
    -webkit-text-stroke: 0;        
    }
}