$negro : #222;

.nav {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-around;
    gap: 200px;
    background-color: #fff;
    align-items: center;
    transition: all .2s;
    .logo {
        display: flex;
        flex-direction: row;
        align-items: center;
        text-decoration: none;
        color: #222;
        transition: .4s;
        img {
            width: 350px;
            transition: .4s;
        }
    }
    .navegacion {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 20px;
        .link {
            color: $negro;
            font-family: 'Raleway', sans-serif;
            font-weight: 550;
            text-decoration: none;
            position: relative;
            padding: 15px;
            transition: .5s ease-in-out;
            &:hover{
                background-color: #e7e7e7;
                &::before {
                    width: 100%;
                }
            }
            &::before{
                content: '';
                position: absolute;
                background-color: #4558BD;
                width: 0%;
                left: 0;
                height: 4px;
                bottom: 0;
                transition: .3s ease-in-out;
            }
        }
    }
    .oculto {
        display: none;
    }
}
    .menu-oculto {
        width: 100%;
        height: 100%;
        background-color: #222e;
        backdrop-filter: blur(5px);
        position: fixed;
        z-index: 100;
        flex-direction: column;
        justify-content: space-evenly;
        transform: translate(100%);
        display: none;
        transition: all .5s;
        .close{
            position: absolute;
            top: 5px;
            right: 5px;
            cursor: pointer;
            color: #e7e7e7;
        }
        .link-rsp {
            color: #e7e7e7;
            font-weight: 600;
            text-decoration: none;
            padding: 40px;
            border-bottom: 1px solid #666;
        }
}

@media screen and (max-width:950px){
    .nav {
        gap: 0;
    }
}

@media screen and (max-width:750px){
    .nav {
        .logo{
            img {
                width: 100%;
            }
        }
        .navegacion {
            gap: 10px;
            .link {
                font-size: 2vw;
            }
        }
    }
}

@media screen and (max-width:650px) {
    .nav {
        height: 50px;
        justify-content: space-between;
        .navegacion {
            gap: 2px;
            .link {
                font-size: 2vw;
            }
        }
    }
}

@media screen and (max-width:550px) {
    .nav {
        .logo {
            width: 35vw;
        }
        .navegacion {
            display: none;
        }
        .oculto {
            display: block;
            .bars{
                cursor: pointer;
                font-size: 35px;
            }
        }
    }
}

.active {
    background-color: #e7e7e7;
    position: relative;
    &::after {
        content: '';
        position: absolute;
        background-color: #4558BD;
        width: 100%;
        left: 0;
        height: 4px;
        bottom: 0;
        transition: .3s ease-in-out;
    }
}