@mixin inputs {
    border: none;
    border-radius: 3px;
    outline: none;
}

form {
    display: flex;
    flex-direction: column;
    background-color: #f7f7f7;
    width: 90%;
    height: 90%;
    justify-content: space-evenly;
    border-radius: 1px;
    box-shadow: 0 0 5px 1px #2225;
    border: 1px solid #333;
    padding: 20px;
    input[type=text]{
        width: 100%;
        height: 45px;
        margin: auto;
        background-color: #e7e7e7;
        box-shadow: 0 0 3px 1px #2223 inset;
        @include inputs()
    }
    input[type=email]{
        width: 100%;
        height: 45px;
        margin: auto;
        background-color: #e7e7e7;
        box-shadow: 0 0 3px 1px #2223 inset;
        @include inputs()
    }
    textarea{
        margin: auto;
        width: 100%;
        height: 110px;
        background-color: #e7e7e7;
        box-shadow: 0 0 3px 1px #2223 inset;
        font-family: inherit;
        resize: none;
        @include inputs()
    }
    input[type=submit]{
        margin: auto;
        height: 45px;
        width: 100%;
        background-color: #4558BD;
        color: #f7f7f7;
        font-size: 20px;
        cursor: pointer;
        &:hover {
            background-color: #4558BDdd;
        }
        @include inputs()
    }
    .error-input {
        margin: 0;
        margin-left: 20px;
        text-align: left;
        color: #dc3545;
        font-weight: bold;
    }
}