.card {
    width: 400px;
    height: 500px;
    background-color: #f9f9f9;
    color: #111;
    box-shadow: 0 0 8px 2px #2225;
    border: 1px solid #777;
}

.img-feed {
    width: 100%;
    height: 350px;
    object-fit: contain;
    border-radius: 2px;
    background-color: #222;
}

.video {
    width: 100%;
    height: 350px;
    object-fit: contain;
    border-radius: 2px;
    background-color: #222;
}

.titulo-img {
    margin: 0 auto;
    font-size: 16px;
    text-align: justify;
}

.fecha-img {
    font-size: 15px;
}

.describ {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
    width: 90%;
    height: max-content;
    min-height: 100px;
    margin: 0;
}

@media screen and (max-width:650px) {
    .card{
        height: max-content;
    }
    .img-feed , .video{
        height: 80vw;
    }
    .titulo-img {
        font-size: 3.5vw;
    }
    .fecha-img {
        font-size: 4vw;
    }
}