.footer {
    background-color: #232323;
    height: 200px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    color: #eee;
    gap: 20px;
    .derechos {
        display: flex;
        flex-direction: column;
        align-items: center;
        img{
            width: 300px;
        }
        h4 {
            margin: 6px;
        }
        p{
            margin: 8px;
        }
    }
    .redes {
        display: flex;
        flex-direction: column;
        .links{
            display: flex;
            flex-direction: column; 
            gap: 15px;
            .link-redes {
                display: flex;
                flex-direction: row;
                align-items: center;
                margin-left: 5px;
                .social-media {
                    width: 25px;
                }
                a{
                    color: #eee;
                    &:hover {
                        color: #1A3BEE;
                    }
                }
            }
        }
    }
    .contacto {
        display: flex;
        flex-direction: column;
        justify-content: left;
        margin-top: 10px;
        h4{
            margin-bottom: 0.5em;
        }
        .datos{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            .text{
                margin: 4px;
                margin-left: 50px;
                a{
                    color: #eee;
                    &:hover{
                        color: #1A3BEE;
                    }
                }
            }
        }
    }
}

@media screen and (max-width:650px){
    .footer {
        height: 160px;
        gap: 5px;
        .derechos{
            padding: 20px 0;
            width: 30%;
            img{
                width: 40vw;
            }
        }
        h4{
            font-size: 4vw;
            margin-bottom: 0;
        }
        p{
            margin: 0;
            font-size: 4vw;
        }
        .redes {
            padding-bottom: 20px;
            h4{
                margin-bottom: 0;
            }
            .links{
                gap: 8px !important;
                margin-top: 10px;
                .link-redes{
                    .social-media {
                        width: 7vw !important;
                    }
                    a{
                        font-size: 3vw;
                    }
                }
            }
        }
        .contacto {
            padding-bottom: 20px;
            .datos{
                .text{
                    margin: 4px;
                    margin-left: 0px;
                }
            }
        }
    }
}

@media screen and (max-width:650px) {
    .footer {
        height: max-content;
        flex-direction: column-reverse;
        align-items: flex-start;
        .redes  {
            width: 100%;
            border-top: 1px solid #f7f7f7;
            .links {
                .link-redes {
                    margin-left: 20px;
                    .social-media {
                        width: 8vw;
                    }
                }
            }
        }
        .contacto {
            margin: 0%;
            width: 100%;
            .datos {
                margin-left: 20px;
                .text {
                    margin: 0;
                }
            }
        }
        .derechos {
            width: 100%;
            border-top: 1px solid #f7f7f7;
        }
    }
}