.portada {
    width: 100%;
    height: 42vw;
    img{
        width: 100%;
        height: 100%;
    }
}

.contenido {
    h2{
        font-size: 6vw;
    }
    .news {
        margin: auto;
        width:800px;
        height: 24vw;
        border-radius: 4px;
        background-color: #fff;
        box-shadow: 0 0 15px 5px #2225;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
        transition: .5s;
        img{
            width: 16vw;
            height: 90%;
            background-color: #d9d9d9;
            border-radius: 2px;
            box-shadow: 0 0 10px 2px #2228;
            transition: .5s;
        }
    }
    .work-mode {
        width: 800px;
        background-color: #fff;
        height: 18vw;
        border-radius: 4px;
        box-shadow: 0 0 15px 5px #2225;
        padding: 10px 0;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
        margin: auto;
        margin-bottom: 100px;
        transition: .5s;
        .mode {
            width: 260px;
            height: 90%;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            align-items: center;
            font-weight: 500;
            letter-spacing: 1px;
            transition: .5s;
            img{
                width: 11vw;
                transition: .5s;
            }
        }
        .line {
            width: 1px;
            height: 100%;
            background-color: #888;
        }
    }
}

@media screen and (max-width:850px){
    .contenido {
        .news {
            width: 80vw;
            height: 30vw;
            img{
                width: 22vw;
            }
        }
        .work-mode {
            width: 80vw;
            height: 25vw;
            .mode {
                width: 25vw;
                height: 90%;
                p{
                    font-size: 3.5vw;
                }
            }
        }
    }
}