.about {
    width: 1100px;
    margin: 50px auto;
    background-color: #f7f7f7;
    border-radius: 3px;
    box-shadow: 0 0 10px 5px #2225;
    padding-bottom: 50px;
    .info-gral {
        width: 90%;
        margin: auto;
        padding-top: 20px;
        display: flex;
        flex-direction: column;
        gap: 40px;
        h3 {
            margin: 0;
            font-size: 40px;
        }
        .info-der {
            text-align: left;
            display: flex;
            gap: 50px;
            flex-direction: column;
            padding-bottom: 55px;
            border-bottom: 1px solid #777;
            .img-xl{
                width: 100%;
                height: 200px;
                object-fit: cover;
                border-radius: 2px;
                box-shadow: 0 0 10px 5px #2225;
            }
            .text {
                h4 {
                    font-size: 25px;
                    margin: 0;
                }
                p{
                    text-align: justify;
                }
            }
        }
        .info-cruzada{
            width: 100%;
            height: max-content;
            display: flex;
            flex-direction: row;
            justify-content: center;
            gap: 100px;
            border-radius: 4px;
            margin: 20px;
            border-bottom: 1px solid #777;
            padding-bottom: 55px;
            .img-m {
                width: 45%;
                height: 300px;
                object-fit: cover;
                box-shadow: 0 0 10px 5px #2225;
            }
            .texto {
                width: 300px;
                text-align: justify;
                h4{
                    font-size: 20px;
                }
            }
        }
        .info-cruzada1{
            width: 100%;
            height: max-content;
            display: flex;
            flex-direction: row-reverse;
            justify-content: center;
            gap: 100px;
            border-radius: 4px;
            margin: 20px;
            .img-m1 {
                width: 45%;
                height: 300px;
                object-fit: cover;
                box-shadow: 0 0 10px 5px #2225;
            }
            .texto1 {
                width: 300px;
                text-align: justify;
                h4{
                    font-size: 20px;
                }
            }
        }
    }
}

@media screen and (max-width:1100px) {
    .about {
        width: 100%;
    }
}

@media screen and (max-width:900px){
    .about {
        padding-bottom: 20px;
        .info-gral{
            width: 90%;
            gap: 20px;
            h3 {
                font-size: 6vw;
            }
            .info-der{
                padding-bottom: 30px;
                gap: 30px;
                .img-xl{
                    margin: auto;
                    width: 100%;
                    height: 20vw;
                }
                .text {
                    h4 {
                        font-size: 3vw;
                    }
                    p{
                        font-size: 2.2vw;
                    }
                }
            }
            .info-cruzada{
                gap: 0;
                transition: all .2s;
                flex-wrap: wrap;
                margin: auto;
                padding-bottom: 20px;
                .img-m{
                    width: 90%;
                    height: 40vw;
                }
                .texto{
                    width: 80vw;
                    h4{
                        font-size: 4vw;
                    }
                    p{
                        font-size: 2.5vw;
                    }
                }
            }
            .info-cruzada1{
                gap: 0;
                transition: all .2s;
                flex-wrap: wrap;
                margin: auto;
                .img-m1{
                    width: 90%;
                    height: 40vw;
                }
                .texto1{
                    width: 80vw;
                    h4{
                        font-size: 4vw;
                    }
                    p{
                        font-size: 2.5vw;
                    }
                }
            }
        }
    }
}

