body {
    background-color: #d7d7d7;
    font-family: 'Raleway' , sans-serif;
}

.App {
    text-align: center;
}

.uper {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #4558BD;
    position: fixed;
    right: 20px;
    bottom: 20px;
    z-index: 500;
    visibility: hidden;
    opacity: 0;
    transition: .3s ease-in-out;
}

.uper-visible {
    visibility: visible;
    opacity: 1;
}

.icon {
    font-size: 45px;
    font-weight: 900;
    color: #e7e7e7;
    cursor: pointer;
}