.galeria {
    .head-title {
        font-size: 60px;
        font-weight: 500;
        margin-bottom: 20px;
    }
    .buscador {
        width: 350px;
        display: flex;
        flex-direction: row;
        align-items: center;
        margin: auto;
        height: 50px;
        justify-content: space-evenly;
        background-color: #f7f7f7;
        border-radius: 4px;
        box-shadow: 0 0 10px 5px #1115;
        .search{
            font-size: 40px;
            font-weight: 700;
            color: #4558BD;
            cursor: pointer;
        }
        .cont-input {
            position: relative;
            display: flex;
            align-items: center;
            .delete {
                color: #FF3A3A;
                position: absolute;
                right: 0;
                cursor: pointer;
                display: none;
            }
            .input {
                height: 35px;
                width: 260px;
                border: none;
                background-color: #dfdede;
                border-radius: 3px;
                &:focus {
                    outline: none;
                }
                &::-webkit-search-cancel-button{
                    display: none;
                }
            }
        }
    }
    .feed {
        width: 80%;
        height: max-content;
        margin: 50px auto;
        background-color: #f7f7f7;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        flex-direction: column;
        padding-bottom: 20px;
        box-shadow: 0 0 10px 5px #2225;
        .fotos {
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;
            flex-direction: column;
            justify-content: space-around;
            width: 100%;
            height: 100%;
            div {
                margin-top: 20px;
                gap: 20px;
                display: flex;  
                flex-wrap: wrap;
                justify-content: space-evenly;
                align-items: center;
            }
            .butons {
                display: flex;
                justify-content: space-around;
                margin-top: 50px;
                .botones {
                    width: 150px;
                    height: 40px;
                    border: none;
                    background-color: #4558BD;
                    border-radius: 3px;
                    color: #f7f7f7;
                    cursor: pointer;
                }
                .inactive {
                    background-color: #777;
                }
            }
        }
    }
}



@media screen and(max-width:650px) {
    .galeria {
        .feed {
            width: 90%;
            padding-bottom: 0;
            .fotos{
                div {
                    margin-top: 0;
                    width: 100%;
                }
            }
            .butons {
                margin: 20px 0;
                .botones {
                    width: 30vw;
                }
            }
        }
        .buscador {
            width: 90%;
            .cont-input {
                .input{
                    width: 90%;
                }
            }
        }
        .head-title {
            font-size: 12vw;
        }
    }
}

@media screen and(max-width:450px) {
    .galeria {
        .feed {
            .fotos {
                .butons {
                    .botones {
                        width: 30vw;
                    }
                }
            }
        }
    }
}